:root{
	--primary-color:#0ab377;
	--secondary-color:#152251;
  --secondary-color-light:#182045;
  --secondary-color-lightr:rgba(24,32,69,0.3);
  --secondary-color-input:#152248;
  --font-color: white;
}
body{
  margin: 0;
  padding: 0;
  color: var(--font-color);
  background-color: var(--secondary-color);
  font-family: 'Poppins',sans-serif;
}
.d-flex2{
	display: flex;
	align-items: flex-start;
}
.swal-modal{
  background-color:rgba(21,34,81,0.8);
  color: var(--font-color);
}

.swal-title,
.swal-text{
  color: var(--font-color);
}
.swal_text{
  margin: 0;
}
.swal-button{
  background-color: var(--primary-color);
  border-radius: 0.2rem;
  outline: none;
  box-shadow: none;
}
.swal-button:hover{
  box-shadow: none;
}
.swal-icon--success:before,
.swal-icon--success:after ,
.swal-icon--success__hide-corners {
  background-color: var(--secondary-color);

}
@media (max-width:500px) {
	.section_wrapper{
		max-width: 100% !important;
	}
}